@charset "utf-8";

/* Overrides for Bootstrap 4 */
body {
    font-size: 0.875rem;
}

.blockquote {
    font-size: 0.875rem;
    padding: 10px 20px;
    margin-bottom: 1rem;
    border-left: 5px solid #eee;
}

.divider {
    height: 1px;
    width:100%;
    display:block; /* for use on default inline elements like span */
    margin: 9px 0;
    overflow: hidden;
    background-color: #e5e5e5;
}

table {
    .MathJax_Display {
        margin-top: 0;
        margin-bottom: 0;
        text-align: left !important;
    }
}

.table-auto {
    width: auto;
}

.explanation {
  margin-bottom: 1rem; 
}
card-body p:last-of-type, .explanation .card-body p:last-of-type {
    margin-bottom: 0; 
}

pre > code {
    padding: 4px;
    border-radius: 4px;
}

.code-header {
    display: block;
    position: relative;
    float: right;
}

.btn-clipboard {
    position: absolute;
    top: .65rem;
    right: .65rem;
    z-index: 10;
    display: block;
}
